.Skills {
  display: flex;
  width: 95%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.SkillsTitle {
  font-size: 4vw;
  font-weight: bold;
}
.SkillItems {
  display: flex;
  justify-content: space-around;
}
.SkillCards {
  display: flex;
  flex-direction: column;
  width: 20%;
}

.SkillCards h1 {
  font-size: 2.5vw;
}
