.Contact {
  width: 100%;
  background-color: #d3d3d3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 15vh;
  height: 25vh;
}

.contactItems {
  width: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.contactSocial {
  width: 30%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.contactSocial a {
  font-size: 1vw;
  text-decoration: none;
  font-style: italic;
  font-weight: 700;
  color: black;
}
