.Bio {
  width: 80%;
  padding-top: 15vh;
  padding-bottom: 15vh;
  align-items: center;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  max-width: 1000px;
}
.bioContent {
  display: flex;
  width: 50%;
  font-size: 2vw;
}
.bioIntro {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: flex-start;
  width: 50%;
}

.bioIntro h1 {
  font-size: 5vw;
}
.bioIntro h2 {
  font-size: 2vw;
}
