.Navbar {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 15vh;
  background-color: rgb(243, 243, 243);
  display: flex;
  align-items: center;
  justify-content: center;
}

.NavbarItems {
  display: flex;
  width: 50%;
  max-width: 400px;
  align-items: center;
  justify-content: space-between;
  font-size: 1.3vw;
}

.Navbar:hover {
  cursor: pointer;
}
